<template>
  <div class="box">
    <el-row :span="24">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title="上传行驶证"></el-step>
        <el-step title="完善表单信息"></el-step>
        <el-step title="提交保存"></el-step>
      </el-steps>
    </el-row>
    <el-row :span="24">
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="100px"
        label-position="left"
      >
        <el-form-item label-width="120px" prop="picture1">
          <span slot="label" style="line-height: 1.2">
            请上传1张清晰行驶证照片(主页)
            <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
              如果没有识别出车牌号 请重新上传识别
            </p>
          </span>
          <!-- <Myuploadimg/> -->
          <MyuploadimgCase
            reqUrl="/openapi/dztz/ocrDrivingLicense"
            parkId="e98963affe174bf98ddb682ba925ff83"
            userName="4105050026"
            @ocrResult="getFile1"
          />
          <!-- <van-uploader
            v-model="picture1fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile1"
            :before-delete="removeFile1"
          />
          <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div> -->
        </el-form-item>
        <el-form-item label-width="120px" prop="picture3">
          <span slot="label">
            请上传1张清晰行驶证照片(副页)
            <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
              如果没有识别出车牌号 请重新上传识别
            </p>
          </span>
          <van-uploader
            v-model="picture3fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile3"
            :before-delete="removeFile3"
          />
          <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>

        <el-form-item
          label-width="120px"
          prop="picture4"
          v-if="this.accessPlatform == 22"
        >
          <span slot="label"> 请上传车辆照片 </span>
          <van-uploader
            v-model="picture4fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile4"
            :before-delete="removeFile4"
          />
          <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
        <el-form-item label-width="120px" prop="picture2">
          <span slot="label"> 请上传清晰的随车清单照片 </span>
          <van-uploader
            v-model="picture2fileList"
            max-count="2"
            accept="image/*"
            :after-read="getFile2"
            :before-delete="removeFile2"
          />
          <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
        <el-form-item label-width="120px" label="车牌号码" prop="plateNumber">
          <el-input
            v-model="plateNumberComputed"
            placeholder="请输入车牌号码"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="品牌型号" prop="vehicleModel">
          <el-input
            v-model="formData.vehicleModel"
            placeholder="请输入品牌型号"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label-width="120px" label="使用性质" prop="useCharacter">
          <el-input
            v-model="formData.useCharacter"
            placeholder="请输入使用性质"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item> -->
        <el-form-item label-width="120px" label="使用性质" prop="useCharacter">
          <el-select
            v-model.number="formData.useCharacter"
            v-model="formData.vehicleModel"
            placeholder="请输入品牌型号"
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="item in selectOptions.useCharacterOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label-width="120px" label="注册日期" prop="registerDate">
          <el-date-picker
            :editable="false"
            v-model="formData.registerDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{ width: '100%' }"
            placeholder="请选择注册日期"
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="发证日期" prop="issueDate">
          <el-date-picker
            :editable="false"
            v-model="formData.issueDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{ width: '100%' }"
            placeholder="请选择发证日期"
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="识别代码" prop="vin">
          <el-input
            v-model="formData.vin"
            placeholder="请输入识别代码"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="车辆类型" prop="vehicleTypeId">
          <el-select
            filterable
            v-model="formData.vehicleTypeId"
            placeholder="请选择车辆类型"
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="(item, index) in vehicleTypeOptions"
              :key="index"
              :label="item.vehicleTypeName"
              :value="item.vehicleTypeId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="所有人" prop="owner">
          <el-input
            v-model="formData.owner"
            placeholder="请输入所有人"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>

        <el-form-item
          label-width="120px"
          label="排放阶段"
          prop="emissionStageId"
        >
          <el-select
            v-model="formData.emissionStageId"
            placeholder="请输入排放阶段"
            clearable
            :style="{ width: '100%' }"
            @change="emissionStageIdChange(formData.emissionStageId)"
          >
            <el-option
              v-for="(item, index) in emissionOptions"
              :key="index"
              :label="item.emissionStageName"
              :value="item.emissionStageId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="燃油类型" prop="fuelType">
          <el-select
            v-model="formData.fuelType"
            placeholder="请输入燃油类型"
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="(item, index) in fuelTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" prop="engineModel">
          <span slot="label">
            <span>
              <span>发动机号码</span>
              <el-tooltip placement="top"></el-tooltip>
            </span>
          </span>
          <el-input
            v-model="formData.engineModel"
            placeholder="请输入发动机号码"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="车牌颜色" prop="plateColor">
          <el-select
            v-model="formData.plateColor"
            placeholder="请输入车牌颜色"
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="(item, index) in plateColorOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label-width="120px" label="总质量" prop="grossMass">
          <el-input
            v-model="formData.grossMass"
            placeholder="请输入总质量"
            clearable
            :style="{ width: '100%' }"
          >
            <template slot="append">KG</template>
          </el-input>
        </el-form-item>
        <el-form-item
          label-width="120px"
          label="核定载质量"
          prop="approvedLoad"
        >
          <el-input
            v-model="formData.approvedLoad"
            placeholder="请输入核定载质量"
            clearable
            :style="{ width: '100%' }"
          >
            <template slot="append">KG</template>
          </el-input>
        </el-form-item>
        <el-form-item
          label-width="120px"
          label="核定载人数"
          prop="appprovedPassengerCapacity"
        >
          <el-input
            v-model="formData.appprovedPassengerCapacity"
            placeholder="请输入核定载人数"
            clearable
            :style="{ width: '100%' }"
          >
            <template slot="append">人</template>
          </el-input>
        </el-form-item>
        <el-form-item
          label-width="120px"
          label="有无OBD"
          prop="isobd"
          v-if="this.accessPlatform == 22"
        >
          <el-radio-group v-model="formData.isobd">
            <el-radio label="1">有</el-radio>
            <el-radio label="2">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label-width="120px" label="地址" prop="address">
          <el-input
            v-model="formData.address"
            placeholder="请输入地址"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="备注" prop="remarks">
          <el-input
            type="textarea"
            v-model="formData.remarks"
            placeholder="请输入备注"
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button type="primary" @click="submitForm">提交</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import * as imageConversion from "image-conversion";

import Myuploadimg from "./uploadimg.vue";
import MyuploadimgCase from "./uploadimgCase.vue";

function getNum(value) {
  if (value.indexOf("+") != -1) {
    let num1 = parseInt(value.split("+")[0]);
    let num2 = parseInt(value.split("+")[1]);
    return num1 + num2;
  } else {
    return value;
  }
}
export default {
  components: { Myuploadimg, MyuploadimgCase },
  data() {
    return {
      selectOptions: {
        useCharacterOptions: [
          {
            value: "A",
            label: "非营运",
          },
          {
            value: "B",
            label: "公路客运",
          },
          {
            value: "C",
            label: "公交客运",
          },
          {
            value: "D",
            label: "出租客运",
          },
          {
            value: "E",
            label: "旅游客运",
          },
          {
            value: "F",
            label: "货运",
          },
          {
            value: "G",
            label: "租赁",
          },
          {
            value: "H",
            label: "警用",
          },
          {
            value: "I",
            label: "消防",
          },
          {
            value: "J",
            label: "救护",
          },
          {
            value: "K",
            label: "工程抢险",
          },
          {
            value: "L",

            label: "营转非",
          },
          {
            value: "M",

            label: "出租转非",
          },
          {
            value: "N",

            label: "教练",
          },
          {
            value: "O",

            label: "幼儿校车",
          },
          {
            value: "P",

            label: "小学生校车",
          },
          {
            value: "Q",

            label: "初中生校车",
          },
          {
            value: "R",

            label: "危险品运输",
          },
          {
            value: "S",

            label: "中小学生校车",
          },
        ],
      },
      parkId: "",
      accessToken: "",
      accessPlatform: "",
      userName: "",
      MBsize: 2,
      active: 0,
      license1: null,
      license2: null,
      showStatus: false,
      formData: {
        isobd: "2",
        picture1: "",
        picture2: "",
        picture3: "",
        picture4: "",
        plateNumber: undefined,
        registerDate: null,
        issueDate: null,
        vin: undefined,
        vehicleTypeId: undefined,
        vehicleModel: undefined, //品牌型号
        useCharacter: undefined, //使用性质
        owner: undefined,
        emissionStageId: undefined,
        fuelType: undefined,
        engineModel: undefined,
        plateColor: undefined,
        grossMass: undefined,
        approvedLoad: undefined,
        appprovedPassengerCapacity: undefined,
        address: undefined,
        remarks: undefined,
      },
      rules: {
        picture1: [
          {
            required: true,
            message: "请上传行驶证(主页)",
            trigger: "blur",
          },
        ],
        picture4: [
          {
            required: true,
            message: "请上车辆照片",
            trigger: "blur",
          },
        ],
        picture3: [
          {
            required: true,
            message: "请上传行驶证(副页)",
            trigger: "blur",
          },
        ],
        picture2: [
          {
            required: true,
            message: "请上传随车清单",
            trigger: "blur",
          },
        ],
        plateNumber: [
          {
            required: true,
            message: "请输入车牌号码",
            trigger: "blur",
          },
        ],
        vehicleModel: [
          {
            required: true,
            message: "请输入品牌型号",
            trigger: "blur",
          },
        ], //品牌型号
        useCharacter: [
          {
            required: true,
            message: "请输入使用性质",
            trigger: "blur",
          },
        ], //使用性质
        registerDate: [
          {
            required: true,
            message: "请选择注册日期",
            trigger: "change",
          },
        ],
        issueDate: [
          {
            required: true,
            message: "请选择发证日期",
            trigger: "change",
          },
        ],
        vin: [
          {
            required: true,
            message: "请输入识别代码",
            trigger: "blur",
          },
        ],
        vehicleTypeId: [
          {
            required: true,
            message: "请选择车辆类型",
            trigger: "change",
          },
        ],
        owner: [
          {
            required: true,
            message: "请输入所有人",
            trigger: "blur",
          },
        ],
        emissionStageId: [
          {
            required: true,
            message: "请输入排放阶段",
            trigger: "blur",
          },
        ],
        fuelType: [
          {
            required: true,
            message: "请输入燃油类型",
            trigger: "blur",
          },
        ],
        engineModel: [
          {
            required: true,
            message: "请输入发动机号码",
            trigger: "blur",
          },
        ],
        plateColor: [
          {
            required: true,
            message: "请输入车牌颜色",
            trigger: "blur",
          },
        ],
        grossMass: [
          {
            required: true,
            message: "请输入总质量",
            trigger: "blur",
          },
          { pattern: /^[1-9]\d*$/, message: "请输入正整数" },
        ],
        approvedLoad: [
          {
            required: true,
            message: "请输入核定载质量",
            trigger: "blur",
          },
          { pattern: /^[1-9]\d*$/, message: "请输入正整数" },
        ],
        appprovedPassengerCapacity: [
          {
            required: true,
            message: "请输入核定载人数",
            trigger: "blur",
          },
          { pattern: /^[+]{0,1}(\d+)$/, message: "请输入正整数" },
        ],
        isobd: [
          {
            required: true,
            message: "请选择有无OBD",
            trigger: "blur",
          },
        ],
        address: [
          {
            required: true,
            message: "请输入地址",
            trigger: "blur",
          },
        ],
        remarks: [
          {
            required: false,
            message: "请输入备注",
            trigger: "blur",
          },
        ],
      },
      picture1fileList: [],
      picture2fileList: [],
      picture3fileList: [],
      picture4fileList: [],
      vehicleTypeOptions: [],
      plateColorOptions: [
        { value: "0", label: "蓝色" },
        { value: "1", label: "黄色" },
        { value: "2", label: "白色" },
        { value: "3", label: "黑色" },
        { value: "4", label: "新能源" },
        { value: "5", label: "其他" },
      ],
      fuelTypeOptions: [
        { value: "A", label: "汽油" },
        { value: "B", label: "柴油" },
        { value: "C", label: "电" },
        { value: "D", label: "混合油" },
        { value: "E", label: "天然气" },
        { value: "F", label: "液化石油气" },
        { value: "L", label: "甲醇" },
        { value: "M", label: "乙醇" },
        { value: "N", label: "太阳能" },
        { value: "O", label: "混合动力" },
        { value: "P", label: "氢" },
        { value: "Q", label: "生物燃料" },
        { value: "Y", label: "无（仅限全挂车等无动力）" },
        { value: "Z", label: "其他" },
      ],
      emissionOptions: [],
    };
  },
  computed: {
    //车牌号自动小写转换为大写英文字母
    plateNumberComputed: {
      get: function () {
        return this.formData.plateNumber;
      },
      set: function (val) {
        this.formData.plateNumber = val.toUpperCase();
      },
    },
  },
  watch: {
    formData: {
      handler: function () {
        if (this.formData.picture1) {
          //步骤条
          this.active = 1;
        } else {
          //步骤条
          this.active = 0;
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.pageInit();
  },
  methods: {
    //排放阶段改变
    emissionStageIdChange(value) {
      switch (value) {
        case "0":
          this.showStatus = false;
          break;
        case "1":
          this.showStatus = false;
          break;
        case "2":
          this.showStatus = false;
          break;
        case "3":
          this.showStatus = false;
          break;
        case "4":
          this.showStatus = false;
          break;
        case "5":
          this.showStatus = true;
          break;
        case "6":
          this.showStatus = true;
          break;
        case "7":
          this.showStatus = true;
          break;
        case "D":
          this.showStatus = true;
          break;
        case "X":
          this.showStatus = false;
          break;

        default:
          break;
      }
    },
    //提交表单
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (valid) {
          this.active = 2;
        } else {
          if (this.formData.picture1) {
            //步骤条
            this.active = 1;
          } else {
            //步骤条
            this.active = 0;
          }
          return false;
        }
        if (this.license1 !== this.license2) {
          this.$alert("行驶证主页副页车牌不同，请重新上传", "提示", {
            confirmButtonText: "确定",
          });
          return;
        }
        this.$confirm("是否确定要提交", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        })
          .then(() => {
            //提交表单接口
            this.Axios.post(`/openapi/dztz/qrcode/saveTransportVehicle`, {
              parkId: this.parkId,
              accessToken: this.accessToken,
              userName: this.userName,
              isobd: this.formData.isobd,
              plateNumber: this.formData.plateNumber,
              vehicleModel: this.formData.vehicleModel, //品牌型号
              useCharacter: this.formData.useCharacter, //使用性质
              registerDate: this.formData.registerDate,
              issueDate: this.formData.issueDate,
              vin: this.formData.vin,
              engineModel: this.formData.engineModel,
              plateColor: this.formData.plateColor,
              vehicleTypeId: this.formData.vehicleTypeId,
              emissionStageId: this.formData.emissionStageId,
              fuelType: this.formData.fuelType,
              owner: this.formData.owner,
              licenseURL: `${this.formData.picture1},${this.formData.picture3}`,
              clzp: this.formData.picture4,
              goodsListURL: this.formData.picture2,
              grossMass: this.formData.grossMass,
              approvedLoad: this.formData.approvedLoad,
              appprovedPassengerCapacity:
                this.formData.appprovedPassengerCapacity,
              address: this.formData.address,
              remarks: this.formData.remarks,
            }).then((data) => {
              if (data.data.code == 200) {
                this.resetForm();
                this.$message({
                  showClose: true,
                  message: "提交成功",
                  type: "success",
                  center: true,
                  offset: 400,
                });
                //步骤条
                this.active = 3;
              }
            });
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: "已取消提交",
              type: "success",
              center: true,
              offset: 400,
            });
          });
      });
    },
    //重置表单
    resetForm() {
      this.$refs["elForm"].resetFields();
      //清空两个图片上传框内的数据
      this.picture1fileList = [];
      this.picture2fileList = [];
      this.picture3fileList = [];
      this.picture4fileList = [];
      //步骤条
      this.active = 0;
    },
    //blob转base64
    getBase64(blob, callback) {
      var a = new FileReader();
      a.onload = function (e) {
        callback(e.target.result);
      };
      a.readAsDataURL(blob);
    },
    //上传图片1 主页
    getFile1(data) {
      if (!data.plateNumber) {
        return this.$message.error("识别失败,请重新上传主页");
      }
      this.license1 = data.plateNumber;
      let date =
        data.registerDate && data.registerDate.length == 8
          ? data.registerDate
          : "";
      let date2 =
        data.issueDate && data.issueDate.length == 8 ? data.issueDate : "";
      if (date != "" && new RegExp(/\d{4}\d{2}\d{2}/).test(date)) {
        this.formData.registerDate =
          date.slice(0, 4) + "-" + date.slice(4, 6) + "-" + date.slice(6, 8);
      }
      if (date2 != "" && new RegExp(/\d{4}\d{2}\d{2}/).test(date2)) {
        this.formData.issueDate =
          date2.slice(0, 4) + "-" + date2.slice(4, 6) + "-" + date2.slice(6, 8);
      }
      //车牌号码
      this.formData.plateNumber = data.plateNumber;
      //品牌型号
      this.formData.vehicleModel = data.vehicleModel;
      //使用性质
      this.formData.useCharacter = data.useCharacter;
      //车辆识别代码
      this.formData.vin = data.vin;
      //车辆类型 这里只给提供了名称没有提供id 所以需要去查找所对应id赋值
      const index = this.vehicleTypeOptions
        .map((item) => item.vehicleTypeName)
        .indexOf(data.vehicleTypeName);
      if (index != -1) {
        this.formData.vehicleTypeId =
          this.vehicleTypeOptions[index].vehicleTypeId;
      }
      //所有人
      this.formData.owner = data.owner;
      //图片地址
      this.formData.picture1 = data.imageURL;
      //发动机号码
      this.formData.engineModel = data.engineModel;
      this.formData.address = data.address;
      //步骤条
      this.active = 1;
    },
    //上传图片3 副页
    getFile3(file, fileList) {
      this.ys(file, 3).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense2`, {
            parkId: this.parkId,
            accessToken: this.accessToken,
            userName: this.userName,
            image: res,
          }).then((res) => {
            const data = res.data.data;
            if (!data.license) {
              return this.$message.error("识别失败,请重新上传副页");
            }
            this.license2 = data.license;
            this.formData.grossMass = data.grossMass.split("kg")[0];
            this.formData.approvedLoad = data.approvedLoad.split("kg")[0];
            this.formData.appprovedPassengerCapacity = getNum(
              data.appprovedPassengerCapacity.split("人")[0]
            );
            this.formData.picture3 = data.imageURL;
            this.active = 1;
          });
        });
      });
    },
    //上传图片2 随车清单照片
    getFile2(file, fileList) {
      this.ys(file, 2).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId: this.parkId,
            accessToken: this.accessToken,
            userName: this.userName,
            image: res,
          }).then((data) => {
            //起初约定好的规则 多张图片用逗号隔开的字符串传输
            if (this.formData.picture2 == "") {
              this.formData.picture2 = data.data.data;
            } else {
              this.formData.picture2 =
                this.formData.picture2 + "," + data.data.data;
            }
          });
        });
      });
    },
    //上传图片4 车辆照片
    getFile4(file, fileList) {
      this.ys(file, 4).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId: this.parkId,
            accessToken: this.accessToken,
            userName: this.userName,
            image: res,
          }).then((data) => {
            //起初约定好的规则 多张图片用逗号隔开的字符串传输
            if (this.formData.picture4 == "") {
              this.formData.picture4 = data.data.data;
            } else {
              this.formData.picture4 =
                this.formData.picture4 + "," + data.data.data;
            }
          });
        });
      });
    },
    //删除图片1 主页
    removeFile1(file, fileList) {
      this.picture1fileList = [];
      this.active = 0;
    },
    //删除图片2 随车清单照片
    removeFile2(file, fileList) {
      this.formData.picture2 = "";
      this.picture2fileList = [];
    },
    //删除图片3 副页
    removeFile3(file, fileList) {
      this.picture3fileList = [];
      this.active = 0;
    },
    //删除图片4 车辆照片
    removeFile4(file, fileList) {
      this.formData.picture4 = "";
      this.picture4fileList = [];
    },
    //根据车牌号查询所有信息
    pageInit() {
      this.parkId = this.$route.query.parkId;
      this.accessToken = this.$route.query.accessToken;
      this.userName = this.$route.query.userName;
      this.accessPlatform = this.$route.query.accessPlatform;
      //车辆类型下拉接口
      this.Axios.post(`/openapi/dztz/getVehicleTypeList`, {
        parkId: this.parkId,
        accessToken: this.accessToken,
        userName: this.userName,
      }).then((data) => {
        this.vehicleTypeOptions = data.data.data;
      });
      //排放阶段下拉接口
      this.Axios.post(`/openapi/dztz/getEmissionStageList`, {
        parkId: this.parkId,
        accessToken: this.accessToken,
        userName: this.userName,
      }).then((data) => {
        this.emissionOptions = data.data.data;
      });
    },
    //把图片文件作为参数传递到方法中 num 1 主页 2 随车清单照片 3 副页 4 车辆照片
    ys(file, num) {
      return new Promise((resolve) => {
        console.log(file);
        let isLt2M = file.file.size / 1024 / 1024 < 2;
        if (isLt2M) {
          resolve(file.file);
        } else {
          imageConversion.compressAccurately(file.file, 800 * 2).then((res) => {
            resolve(res);
          });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.box {
  padding: 10px;
}
/deep/.hide .el-upload--picture-card {
  display: none;
}
.el-form-item--medium .el-form-item__content,
.el-form-item--medium /deep/.el-form-item__label {
  line-height: 20px;
}
/deep/.el-upload__tip {
  margin-top: 0;
}
.el-row {
  margin-bottom: 20px;
}
</style>



