<template>
  <div class="uploadimg">
    <div class="upload">
      <img
        :src="imgSrc?imgSrc:uploadBtnIcon"
        alt="拍照上传"
        @click="handleLaunchCamera()"
      />
    </div>
    <div class="camera" v-if="isCamera">
      <video
        ref="photo"
        webkit-playsinline="true"
        x-webkit-airplay="true"
        x5-playsinline="true"
        playsinline="true"
        id="videoCamera"
        :width="videoWidth"
        :height="videoHeight"
        autoplay
      ></video>
      <div class="shade_warp">
        <canvas
          style="display: none"
          :width="canvasWidth"
          :height="canvasHeight"
          id="canvasCamera"
        >
        </canvas>
        <img
          class="camera_border"
          src="../assets/icon-border.png"
          alt="取景框"
        />
        <p class="camera_tips">请按图示将证件放入取景框中</p>
        <div class="tools">
          <button id="photo">相册</button>
          <button class="suerCapture" @click="setImage()">拍照</button>
          <button id="cancelCapture" @click="handleCloseCamera()">取消</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import uploadBtnIcon from '../assets/uploadBtnIcon.png'
export default {
  props: {
    reqUrl: String,
    parkId: String,
    userName: String,
  },
  data() {
    return {
      uploadBtnIcon,
      isCamera: false, // 是否拍照
      videoWidth: 300,
      videoHeight: 400,
      canvasWidth: 300,
      canvasHeight: 400,
      imgSrc: "",
      thisCancas: null,
      thisContext: null,
      thisVideo: null,
    };
  },
  mounted() {
  },
  methods: {
    handleLaunchCamera() {
      this.isCamera = true;
      // 进行各种数据初始化
      this.getCompetence();
    },
    handleCloseCamera() {
      this.isCamera = false;
    },
    getCompetence() {
      const _this = this;
      this.$nextTick(() => {
        // this.videoWidth = this.$refs.photo.clientWidth; //宽度
        // this.videoHeight = this.videoWidth;
        this.thisCancas = document.getElementById("canvasCamera");
        this.thisContext = this.thisCancas.getContext("2d");
        this.thisVideo = document.getElementById("videoCamera");
      });
      // 旧版本浏览器可能根本不支持mediaDevices，我们首先设置一个空对象
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }
      // 一些浏览器实现了部分mediaDevices，我们不能只分配一个对象
      // 使用getUserMedia，因为它会覆盖现有的属性。
      // 这里，如果缺少getUserMedia属性，就添加它。
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          // 首先获取现存的getUserMedia(如果存在)
          var getUserMedia =
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia ||
            navigator.getUserMedia;
          // 有些浏览器不支持，会返回错误信息
          // 保持接口一致
          if (!getUserMedia) {
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }
          // 否则，使用Promise将调用包装到旧的navigator.getUserMedia
          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }
      if (window.stream) {
        //一进来的时候判断是否开着摄像头
        window.stream.getTracks().forEach((track) => {
          track.stop();
        });
      }
      var constraints = {
        //配置默认前置摄像头,以及手机上摄像头取到的画面宽高
        audio: true,
        video: {
          width: this.videoWidth,
          height: this.videoHeight,
          transform: "scaleX(-1)",
          sourceId: "default",
          facingMode: { exact: "environment" },
          // facingMode: "user"
        },
      };
      var obj = {
        audio: true,
        video: { facingMode: { exact: "environment" } },
      };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function (stream) {
          // 旧的浏览器可能没有srcObject
          if ("srcObject" in _this.thisVideo) {
            _this.$nextTick(() => {
              _this.thisVideo.srcObject = stream; //that.video是video标签节点，请自行获取节点，updated周期里可以拿到！
              _this.thisVideo.play();
            });
          } else {
            // 避免在新的浏览器中使用它，因为它正在被弃用。
            _this.thisVideo.src = window.URL.createObjectURL(stream);
          }
          // _this.thisVideo.onloadedmetadata = function (e) {
          //   _this.thisVideo.play();
          // };
        })
        .catch((err) => {
          console.log("错误");
          console.log(err);
        });
    },
    //  绘制图片（拍照功能）
    setImage() {
      const _this = this;
      // 点击，canvas画图
      _this.thisContext.scale(-1, 1); //如果你上传图片是镜像的加上这段，我这边图片上传后会镜像
      this.thisContext.drawImage(
        _this.thisVideo,
        -_this.canvasWidth, //镜像翻转要往负方向移动图片的距离
        0,
        _this.canvasWidth,
        _this.canvasHeight
      );
      // 获取图片base64链接
      const image = this.thisCancas.toDataURL("image/jpeg", 1);
      _this.imgSrc = image;
      _this.upload(image);
      // 停止摄像机
      _this.thisVideo.pause();
    },
    // base64转文件
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(",");
      var mime = arr[0].match(/:(.*?);/)[1];
      var bstr = atob(arr[1]);
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {
        type: mime,
      });
    },
    // 上传图片
    upload(image) {
      this.Axios.post(this.reqUrl, {
          parkId: this.parkId,
          userName: this.userName,
          image: image,
        })
        .then((res) => {
          this.$emit('ocrResult',res.data)
          this.handleCloseCamera();
        });
    },
  },
};
</script>
<style lang="less" scoped>
.uploadimg {
  .upload {
    img {
      width: 80px;
      height: 80px;
    }
  }
  .camera {
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 99;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.9);
    #videoCamera {
      margin: 10px auto;
      display: block;
    }
    .shade_warp {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      #canvasCamera {
        margin: 10px auto;
      }
      .camera_border {
        display: block;
        width: 300px;
        height: 400px;
        margin: 10px auto;
      }
      .camera_tips {
        text-align: center;
        margin: 0;
        color: #fff;
        line-height: 1.5;
      }
      .tools {
        display: flex;
        justify-content: space-around;
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 10px;
        button {
          border-radius: 50%;
          border: 1px solid #dcdcdc;
          width: 60px;
          height: 60px;
        }
      }
    }
  }
}
</style>



