<template>
  <div id="box">
    <el-row :style="{ width: '100%' }">
      <router-link
        :to="{
          path: '/carRegister',
          query: {
            parkId: parkId,
            accessToken: accessToken,
            userName: userName,
            accessPlatform: accessPlatform,
          },
        }"
      >
        <el-button type="primary"> 添加运输车辆 </el-button>
      </router-link>
    </el-row>
    <el-row>
      <router-link
        :to="{
          path: '/fieldCarRegister',
          query: {
            parkId: parkId,
            accessToken: accessToken,
            userName: userName,
            accessPlatform: accessPlatform,
          },
        }"
      >
        <el-button type="success"> 添加场内运输车辆 </el-button>
      </router-link>
    </el-row>
    <el-row>
      <router-link
        :to="{
          path: '/mechanicsCarRegister',
          query: {
            parkId: parkId,
            accessToken: accessToken,
            userName: userName,
            accessPlatform: accessPlatform,
          },
        }"
      >
        <el-button type="info"> 添加非道路移动器械 </el-button>
      </router-link>
    </el-row>
  </div>
</template>

<script>
//获取地址栏中的参数
const quryToObj = (url) => {
  var params = url.split("?")[1].split("&");
  var obj = {};
  params.map((v) => (obj[v.split("=")[0]] = v.split("=")[1]));
  return obj;
};
export default {
  name: "vehicleType",
  data() {
    return {
      parkId: "",
      accessToken: "",
      userName: "",
      accessPlatform: "",
    };
  },
  mounted() {
    this.pageInit();
  },
  methods: {
    // },http://parktest.leting360.cn/app/index.html?
    // parkId=2f054100f817e7e66b658eb01f6d67c9&accessToken=37CB3B3A1D04738F2263F440EBE14BA8&userName=13203468877
    //页面初始化
    pageInit() {
      let testUrl =
        "http://parktest.leting360.cn/index.html#?parkId=2628ee995d6da9769f8e5ae9dc3d10a7&accessToken=41040300140120110600001&userName=TEST&accessPlatform=22#/";
      console.log(window.location.href, "地址栏url");
      let url = window.location.href;
      //这里的url要切换
      let urlData = quryToObj(url.split("#/")[0]);
      this.parkId = urlData.parkId;
      this.accessToken = urlData.accessToken;
      this.userName = urlData.userName;
      this.accessPlatform = urlData.accessPlatform;
    },
  },
};
</script>

<style scoped>
#box{
  padding: 0 20px;
}
.el-row {
  margin-top: 20px;
}
.el-button {
  width: 100%;
}
a {
  color: white;
  text-decoration: none;
}

.router-link-active {
  text-decoration: none;
}
</style>



