<template>
  <div class="box">
    <el-row :span="24">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title="上传行驶证"></el-step>
        <el-step title="完善表单信息"></el-step>
        <el-step title="提交保存"></el-step>
      </el-steps>
    </el-row>
    <el-row :span="24">
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="100px"
        label-position="left"
      >
      <!--请上传整机铭牌照片-->
        <el-form-item label-width="120px" prop="picture1">
          <span slot="label">
            请上传整机铭牌照片
          </span>
          <van-uploader
            v-model="picture1fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile1"
            :before-delete="removeFile1"
          />
        <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
<!--  请上传机械环保标签-->
        <el-form-item label-width="120px" prop="picture2">
          <span slot="label">
            请上传机械环保标签
          </span>
           <van-uploader
            v-model="picture2fileList"
            max-count="2"
            accept="image/*"
            :after-read="getFile2"
            :before-delete="removeFile2"
          />
         <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
        <!-- 请上发动机铭牌照片-->
        <el-form-item label-width="120px" prop="picture3">
          <span slot="label">
            请上发动机铭牌照片
          </span>
           <van-uploader
            v-model="picture3fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile3"
            :before-delete="removeFile3"
          />
        </el-form-item>

        <el-form-item label-width="120px" label="登记编码" prop="registerCode">
          <el-input
            v-model="formData.registerCode"
            placeholder="请输入登记编码"
            clearable
            :style="{width: '100%'}"

          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="环保代码" prop="environmentalCode">
          <el-input
            v-model="formData.environmentalCode"
            placeholder="请输入环保代码"
            clearable
            :style="{width: '100%'}"

          ></el-input>
        </el-form-item>
        <!-- 注册日期-->
        <el-form-item label-width="120px" label="注册日期" prop="registerDate">
          <el-date-picker
            :editable="false"
            v-model="formData.registerDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder="请选择注册日期"
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="生产日期" prop="productDate">
          <el-date-picker
            :editable="false"
            v-model="formData.productDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder="请选择生产日期"
            clearable
          ></el-date-picker>
        </el-form-item>

        <el-form-item label-width="120px" label="所有人/单位" prop="owner">
          <el-input
            v-model="formData.owner"
            placeholder="请输入所有人"
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="机械种类" prop="mechanicType">
          <el-select
            v-model="formData.mechanicType"
            placeholder="请选择机械种类"
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in mechanicTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="场内机械状态" prop="mechanicState">
          <el-select
            v-model="formData.mechanicState"
            placeholder="请选择场内机械状态"
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in mechanicStateOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="排放阶段" prop="emissionStageId">
          <el-select
            v-model="formData.emissionStageId"
            placeholder="请输入排放阶段"
            clearable
            :style="{width: '100%'}"
            @change="emissionStageIdChange(formData.emissionStageId)"
          >
            <el-option
              v-for="(item, index) in emissionOptions"
              :key="index"
              :label="item.emissionStageName"
              :value="item.emissionStageId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="燃油类型" prop="fuelType">
          <el-select
            v-model="formData.fuelType"
            placeholder="请输入燃油类型"
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in fuelTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>


        <el-form-item label-width="120px" label="备注" prop="remarks">
          <el-input
            type="textarea"
            v-model="formData.remarks"
            placeholder="请输入备注"
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button type="primary" @click="submitForm">提交</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import * as imageConversion from "image-conversion"

function getNum(value) {
  if (value.indexOf("+") != -1) {
    let num1 = parseInt(value.split("+")[0])
    let num2 = parseInt(value.split("+")[1])
    return num1 + num2
  } else {
    return value
  }
}
// let parkId = "";
// let accessToken = "";
// let userName = "";
//获取地址栏中的参数
export default {
  components: {},
  props: [],
  data() {
    return {
      parkId:'',
      accessToken:'',
      userName:'',
      MBsize: 18,
      active: 0,
      license1:null,
      license2:null,
      showStatus:false,
      formData: {
        picture1: "",
        picture2: "",
        picture3: "",
        machinePlatePhoto: undefined,//整车铭牌照片
        greenLablePhoto: undefined,//机械环保标签
        engineImageURL: undefined,//发动机铭牌图片
        registerCode: undefined,//环保登记编码
        environmentalCode: undefined,//机械环保代码
        registerDate: null,//注册日期
        productDate: null,//生产日期
        owner: undefined,//所有人
        mechanicType: undefined,//机械种类
        mechanicState: undefined,//场内机械状态
        emissionStageId: undefined,//排放阶段Id
        fuelType:undefined,//燃料类型
        remarks: undefined,//备注
      },
      rules: {
        picture3: [
          {
            required: true,
            message: "请上传发动机铭牌图片",
            trigger: "blur",
          },
        ],
        picture1: [
          {
            required: true,
            message: "请上传整车铭牌照片",
            trigger: "blur",
          },
        ],
        picture2: [
          {
            required: true,
            message: "请上传机械环保标签",
            trigger: "blur",
          },
        ],
        registerCode: [
          {
            required: true,
            message: "请输入登记编码",
            trigger: "blur",
          },
        ],
        environmentalCode: [
          {
            required: true,
            message: "请输入环保代码",
            trigger: "blur",
          },
        ],
        registerDate: [
          {
            required: true,
            message: "请选择注册日期",
            trigger: "change",
          },
        ],
        productDate:[
          {
            required: true,
            message: "请选择生产日期",
            trigger: "change",
          },
        ],
        owner: [
          {
            required: true,
            message: "请输入所有人",
            trigger: "blur",
          },
        ],
        mechanicType: [
          {
            required: true,
            message: "请选择机械种类",
            trigger: "blur",
          },
        ],
        mechanicState: [
          {
            required: true,
            message: "请选择场内机械状态",
            trigger: "blur",
          },
        ],
        emissionStageId: [
          {
            required: true,
            message: "请选择排放阶段",
            trigger: "blur",
          },
        ],
        fuelType:[
          {
            required: true,
            message: "请输入燃油类型",
            trigger: "blur",
          },
        ],

        remarks: [
          {
            required: false,
            message: "请输入备注",
            trigger: "blur",
          },
        ],
      },
      picture1fileList: [],
      picture2fileList: [],
      picture3fileList: [],
      vehicleTypeOptions: [],
      fuelTypeOptions:[
        {value:"A",label:"汽油"},
        {value:"B",label:"柴油"},
        {value:"C",label:"电"},
        {value:"D",label:"混合油"},
        {value:"E",label:"天然气"},
        {value:"F",label:"液化石油气"},
        {value:"L",label:"甲醇"},
        {value:"M",label:"乙醇"},
        {value:"N",label:"太阳能"},
        {value:"O",label:"混合动力"},
        {value:"P",label:"氢"},
        {value:"Q",label:"生物燃料"},
        {value:"Y",label:"无（仅限全挂车等无动力）"},
        {value:"Z",label:"其他"},
      ],
      mechanicTypeOptions:[
        {value:"1",label:"装载机"},
        {value:"2",label:"叉车"},
        {value:"3",label:"泵车"},
        {value:"4",label:"清扫车"},
        {value:"5",label:"其他"},
      ],
      mechanicStateOptions:[
        {value:"1",label:"正常"},
        {value:"2",label:"封存"},
        {value:"3",label:"注销"},
      ],
      emissionOptions: [],
    };
  },
  
  watch: {
    formData: {
      handler: function () {
        this.$refs["elForm"].validate((valid) => {
          if (valid) {
            //步骤条
            this.active = 2;
          } else {
            if (this.formData.picture1 !== "") {
              //步骤条
              this.active = 1;
            } else {
              //步骤条
              this.active = 0;
            }
          }
        });
      },
      deep: true,
    },
  },
  mounted() {
    this.pageInit();
  },
  methods: {
    //排放阶段改变
    emissionStageIdChange(value){
      switch (value) {
        case "0":
          this.showStatus = false
          break;
        case "1":
          this.showStatus = false
          break;
        case "2":
          this.showStatus = false
          break;
        case "3":
          this.showStatus = false
          break;
        case "4":
          this.showStatus = false
          break;
        case "5":
          this.showStatus = true
          break;
        case "6":
          this.showStatus = true
          break;
        case "7":
          this.showStatus = true
          break;
        case "D":
          this.showStatus = true
          break;
        case "X":
          this.showStatus = false
          break;

        default:
          break;
      }
    },
    //提交表单
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid){
          this.$alert('请检查所有输入框是否填写完毕', '提示', {
            confirmButtonText: '确定',
          });
          return
        }
        if(this.license1!==this.license2){
          this.$alert('行驶证主页副页车牌不同，请重新上传', '提示', {
            confirmButtonText: '确定',
          });
          return;
        }
        this.$confirm("是否确定要提交", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        })
          .then(() => {
            //提交表单接口
            //this.Axios.post(`openapi/dztz/qrcode/saveNoRoadMoveVehicle`
            this.Axios.post(`/openapi/dztz/qrcode/saveNoRoadMoveVehicle`, {
              parkId:this.parkId,
              accessToken:this.accessToken,
              userName:this.userName,
              machinePlatePhoto: this.formData.machinePlatePhoto,//整车铭牌照片
              greenLablePhoto: this.formData.greenLablePhoto,//机械环保标签
              engineImageURL: this.formData.engineImageURL,//发动机铭牌图片
              registerCode: this.formData.registerCode,//环保登记编码
              environmentalCode: this.formData.environmentalCode,//机械环保代码
              registerDate: this.formData.registerDate,//注册日期
              productDate: this.formData.productDate,//生产日期
              owner: this.formData.owner,//所有人
              mechanicType: this.formData.mechanicType,//机械种类
              mechanicState: this.formData.mechanicState,//场内机械状态
              emissionStageId: this.formData.emissionStageId,//排放阶段Id
              fuelType:this.formData.machinePlatePhoto,//燃料类型
              remarks: this.formData.remarks,//备注
            }).then((data) => {
              if (data.data.code == 200) {
                this.resetForm()
                this.$message({
                  showClose: true,
                  message: "提交成功",
                  type: "success",
                  center: true,
                  offset: 400,
                });
                //步骤条
                this.active = 3;

              }
            });
            // TODO 提交表单
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: "已取消提交",
              type: "success",
              center: true,
              offset: 400,
            });
          });
      });
    },
    //重置表单
    resetForm() {
      this.$refs["elForm"].resetFields();
      //清空两个图片上传框内的数据
      this.picture1fileList = [];
      this.picture2fileList = [];
      this.picture3fileList = [];
      //步骤条
      this.active = 0;
    },
    //blob转base64
    getBase64(blob, callback) {
      var a = new FileReader();
      a.onload = function (e) {
        callback(e.target.result);
      };
      a.readAsDataURL(blob);
    },
     //删除图片1
    removeFile1(file, fileList) {
      this.formData.picture1 = "";
      this.picture1fileList = [];
    },
      //删除图片2
    removeFile2(file, fileList) {
      this.formData.picture2 = "";
      this.picture2fileList = [];
    },
      //删除图片3
    removeFile3(file, fileList) {
      this.formData.picture3 = "";
      this.picture3fileList = [];
    },
    //上传图片1
    getFile1(file, fileList) {
      this.hideUploadEdit1 = fileList.length >= 2
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            //起初约定好的规则 多张图片用逗号隔开的字符串传输
            if (this.formData.picture1 == "") {
              this.formData.picture1 = data.data.data;
            } else {
              this.formData.picture1 =
                this.formData.picture1 + "," + data.data.data;
            }
          });
        });
      });
    },
    //上传图片2
    getFile2(file, fileList) {
      this.hideUploadEdit2 = fileList.length >= 2
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            //起初约定好的规则 多张图片用逗号隔开的字符串传输
            if (this.formData.picture2 == "") {
              this.formData.picture2 = data.data.data;
            } else {
              this.formData.picture2 =
                this.formData.picture2 + "," + data.data.data;
            }
          });
        });
      });
    },
    //上传图片3
    getFile3(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            //起初约定好的规则 多张图片用逗号隔开的字符串传输
            if (this.formData.picture3 == "") {
              this.formData.picture3 = data.data.data;
            } else {
              this.formData.picture3 =
                this.formData.picture3 + "," + data.data.data;
            }
          });
        });
      });
    },
    pageInit() {
      this.parkId = this.$route.query.parkId;
      this.accessToken = this.$route.query.accessToken;
      this.userName = this.$route.query.userName;
      //车辆类型下拉接口
      this.Axios.post(`/openapi/dztz/getVehicleTypeList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.vehicleTypeOptions = data.data.data;
      });
      //排放阶段下拉接口
      this.Axios.post(`/openapi/dztz/getEmissionStageList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.emissionOptions = data.data.data;
      });
    },
  //把图片文件作为参数传递到方法中 num 1 主页 2 随车清单照片 3 副页 4 车辆照片
    ys(file, num) {
      return new Promise((resolve) => {
        let isLt2M = file.file.size / 1024 / 1024< 2;
        if (isLt2M) {
          resolve(file.file);
        } else {
          imageConversion
             .compressAccurately(file.file, 800*2)
            .then((res) => {
              resolve(res);
            });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.box {
  padding: 10px;
}
/deep/.hide .el-upload--picture-card {
  display: none;
}

.el-row {
  margin-bottom: 20px;
}
</style>



