<template>
  <div class="box">
    <el-row :span="24">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title="上传行驶证"></el-step>
        <el-step title="完善表单信息"></el-step>
        <el-step title="提交保存"></el-step>
      </el-steps>
    </el-row>
    <el-row :span="24">
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="100px"
        label-position="left"
      >
        <el-form-item label-width="120px" prop="">
            <span slot="label" style="line-height: 1.2">
            请上传1张清晰行驶证照片(主页)
            <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
              如果没有识别出车牌号 请重新上传识别
            </p>
          </span>
           <van-uploader
            v-model="picture1fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile1"
            :before-delete="removeFile1"
          />
          <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
        <el-form-item label-width="120px" prop="">
           <span slot="label">
            请上传1张清晰行驶证照片(副页)
           <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
              如果没有识别出车牌号 请重新上传识别
            </p>
          </span>
            <van-uploader
            v-model="picture3fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile3"
            :before-delete="removeFile3"
          />
            <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
        <el-form-item label-width="120px" prop="picture2" v-if="showStatus">
          <span slot="label">
            请上传清晰的随车清单照片
          </span>
           <van-uploader
            v-model="picture2fileList"
            max-count="2"
            accept="image/*"
            :after-read="getFile2"
            :before-delete="removeFile2"
          />
          <div class="el-upload__tip">只能上传不超过 {{ MBsize }}MB 的图片</div>
        </el-form-item>
        <el-form-item label-width="120px" label="车牌/内部号码" prop="plateNumber">
          <el-input
            v-model="plateNumberComputed"
            placeholder="请输入车牌号码"
            clearable
            :style="{width: '100%'}"

          ></el-input>
        </el-form-item>
        <!-- 注册日期-->
        <el-form-item label-width="120px" label="注册日期" prop="registerDate">
          <el-date-picker
            :editable="false"
            v-model="formData.registerDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder="请选择注册日期"
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="生产日期" prop="productDate">
          <el-date-picker
            :editable="false"
            v-model="formData.productDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder="请选择生产日期"
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="车辆VIN" prop="vin">
          <el-input v-model="formData.vin" placeholder="请输入车辆识别代码VIN" clearable :style="{width: '100%'}"></el-input>
        </el-form-item>

        <el-form-item label-width="120px" prop="engineModel">
          <span slot="label">
            <span>
              <span>发动机号码</span>
              <el-tooltip placement="top"></el-tooltip>
            </span>
          </span>
          <el-input
            v-model="formData.engineModel"
            placeholder="请输入发动机号码"
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="环保登记编码" prop="registerCode">
          <el-input
            v-model="formData.registerCode"
            placeholder="请输入环保登记编码"
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="所有人" prop="owner">
          <el-input
            v-model="formData.owner"
            placeholder="请输入所有人"
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="排放阶段" prop="emissionStageId">
          <el-select
            v-model="formData.emissionStageId"
            placeholder="请输入排放阶段"
            clearable
            :style="{width: '100%'}"
            @change="emissionStageIdChange(formData.emissionStageId)"
          >
            <el-option
              v-for="(item, index) in emissionOptions"
              :key="index"
              :label="item.emissionStageName"
              :value="item.emissionStageId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="燃油类型" prop="fuelType">
          <el-select
            v-model="formData.fuelType"
            placeholder="请输入燃油类型"
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in fuelTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <div v-if="this.accessPlatform == 22">
          <el-form-item label-width="120px" label="有无OBD" prop="isobd">
            <el-radio-group v-model="formData.isobd">
              <el-radio label="1">有</el-radio>
              <el-radio label="2">无</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label-width="120px" label="OBD安装时间" prop="obdTime" v-if="this.formData.isobd == '1'">
            <el-col>
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="formData.obdTime"
                style="width: 100%;"
              >
              </el-date-picker>
            </el-col>
          </el-form-item>
        </div>


        <el-form-item label-width="120px" label="备注" prop="remarks">
          <el-input
            type="textarea"
            v-model="formData.remarks"
            placeholder="请输入备注"
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button type="primary" @click="submitForm">提交</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import * as imageConversion from "image-conversion"

function getNum(value) {
  if (value.indexOf("+") != -1) {
    let num1 = parseInt(value.split("+")[0])
    let num2 = parseInt(value.split("+")[1])
    return num1 + num2
  } else {
    return value
  }
}

//获取地址栏中的参数
// const quryToObj = (url) => {
//   var params = url.split("?")[1].split("&");
//   var obj = {};
//   params.map((v) => (obj[v.split("=")[0]] = v.split("=")[1]));
//   return obj;
// };
export default {
  components: {},
  props: [],
  data() {
    return {
      parkId : '',
      accessToken : '',
      accessPlatform:'',
      userName : '',
      MBsize: 18,
      active: 0,
      license1:null,
      license2:null,
      showStatus:false,
      formData: {
        picture1: "",
        picture2: "",
        picture3: "",
         //goodsListURL,//随车清单
        // licenseURL//行驶证
        // gatewayId//可用车道
        isobd:'',
        obdTime:'',
        productDate:undefined,//生产日期
        plateNumber: undefined,//车牌号码
        registerDate: null,//注册日期
        vin: undefined,//车辆识别代码VIN
        owner: undefined,//所有人
        registerCode:'',//环保等级编码
        emissionStageId: undefined,//排放阶段
        fuelType:undefined,//燃料类型
        engineModel: undefined,//发动机型号
        remarks: undefined,//备注
      },
      rules: {
        plateNumber: [
          {
            required: true,
            message: "请输入车牌号码",
            trigger: "blur",
          },
        ],
        registerDate: [
          {
            required: true,
            message: "请选择注册日期",
            trigger: "change",
          },
        ],
        productDate: [
          {
            required: true,
            message: "请选择生产日期",
            trigger: "change",
          },
        ],
        vin: [
          {
            required: true,
            message: "请输入识别代码",
            trigger: "blur",
          },
        ],
        registerCode: [
          {
            required: true,
            message: "请输入环保登记编码",
            trigger: "change",
          },
        ],
        isobd:[
          {
            required: true,
            message: "请选择有无OBD",
            trigger: "blur",
          }
        ],
        obdTime:[
          {
            required: true,
            message: "请选择OBD安装时间",
            trigger: "blur",
          }
        ],
        owner: [
          {
            required: true,
            message: "请输入所有人",
            trigger: "blur",
          },
        ],
        emissionStageId: [
          {
            required: true,
            message: "请输入排放阶段",
            trigger: "blur",
          },
        ],
        fuelType:[
          {
            required: true,
            message: "请输入燃油类型",
            trigger: "blur",
          },
        ],
        engineModel: [
          {
            required: true,
            message: "请输入发动机号码",
            trigger: "blur",
          },
        ],
        remarks: [
          {
            required: false,
            message: "请输入备注",
            trigger: "blur",
          },
        ],
      },
      picture1fileList: [],
      picture2fileList: [],
      picture3fileList: [],
      fuelTypeOptions:[
        {value:"A",label:"汽油"},
        {value:"B",label:"柴油"},
        {value:"C",label:"电"},
        {value:"D",label:"混合油"},
        {value:"E",label:"天然气"},
        {value:"F",label:"液化石油气"},
        {value:"L",label:"甲醇"},
        {value:"M",label:"乙醇"},
        {value:"N",label:"太阳能"},
        {value:"O",label:"混合动力"},
        {value:"P",label:"氢"},
        {value:"Q",label:"生物燃料"},
        {value:"Y",label:"无（仅限全挂车等无动力）"},
        {value:"Z",label:"其他"},
      ],
      emissionOptions: [],
      // wxtip:false
    };
  },
  computed: {
    //车牌号自动小写转换为大写英文字母
    plateNumberComputed: {
      get: function () {
        return this.formData.plateNumber;
      },
      set: function (val) {
        this.formData.plateNumber = val.toUpperCase();
      },
    },
  },
  watch: {
    formData: {
      handler: function () {
        this.$refs["elForm"].validate((valid) => {
          if (valid) {
            //步骤条
            this.active = 2;
          } else {
            if (this.formData.picture1 !== "") {
              //步骤条
              this.active = 1;
            } else {
              //步骤条
              this.active = 0;
            }
          }
        });
      },
      deep: true,
    },
  },
  created() {},
  mounted() {
    // let ua = navigator.userAgent.toLowerCase();
    // let isWeixin = ua.indexOf('micromessenger') != -1;
    // if (isWeixin) {
    //   this.wxtip = true
    // }else{
    // }
    this.pageInit();
  },
  methods: {
    //排放阶段改变
    emissionStageIdChange(value){
      switch (value) {
        case "0":
          this.showStatus = false
          break;
        case "1":
          this.showStatus = false
          break;
        case "2":
          this.showStatus = false
          break;
        case "3":
          this.showStatus = false
          break;
        case "4":
          this.showStatus = false
          break;
        case "5":
          this.showStatus = true
          break;
        case "6":
          this.showStatus = true
          break;
        case "7":
          this.showStatus = true
          break;
        case "D":
          this.showStatus = true
          break;
        case "X":
          this.showStatus = false
          break;

        default:
          break;
      }
    },
    //提交表单
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid){
          this.$alert('请检查所有输入框是否填写完毕', '提示', {
            confirmButtonText: '确定',
          });
          return
        }
        if(this.license1!==this.license2){
          this.$alert('行驶证主页副页车牌不同，请重新上传', '提示', {
            confirmButtonText: '确定',
          });
          return;
        }
        this.$confirm("是否确定要提交", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        })
          .then(() => {
            //提交表单接口
            //this.Axios.post(`openapi/dztz/qrcode/saveOnSideTransportVehicle`
            this.Axios.post(`/openapi/dztz/qrcode/saveOnSideTransportVehicle`, {
              parkId:this.parkId,
              accessToken:this.accessToken,
              userName:this.userName,
              plateNumber: this.formData.plateNumber,//车牌号码
              registerDate: this.formData.registerDate,//注册日期
              productDate: this.formData.productDate,//生产日期
              vin: this.formData.vin,//车辆识别代码VIN
              engineModel: this.formData.engineModel,//发动机型号
              emissionStageId: this.formData.emissionStageId,//排放阶段
              fuelType: this.formData.fuelType,//燃料类型
              isobd:this.formData.isobd,
              obdTime:this.formData.obdTime,
              owner: this.formData.owner,//所有人
              registerCode:this.formData.registerCode,//环保编码
              licenseURL: `${this.formData.picture1},${this.formData.picture3}`,//行驶证（照片URL）
              goodsListURL: this.formData.picture2,//随车清单（照片URL）
              remarks: this.formData.remarks,//备注
            }).then((data) => {
              if (data.data.code == 200) {
                this.resetForm()
                this.$message({
                  showClose: true,
                  message: "提交成功",
                  type: "success",
                  center: true,
                  offset: 400,
                });
                //步骤条
                this.active = 3;

              }
            });
            // TODO 提交表单
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: "已取消提交",
              type: "success",
              center: true,
              offset: 400,
            });
          });
      });
    },
    //重置表单
    resetForm() {
      this.$refs["elForm"].resetFields();
      //清空两个图片上传框内的数据

      this.picture1fileList = [];
      this.picture2fileList = [];
      this.picture3fileList = [];
      //步骤条
      this.active = 0;
    },
    //blob转base64
    getBase64(blob, callback) {
      var a = new FileReader();
      a.onload = function (e) {
        callback(e.target.result);
      };
      a.readAsDataURL(blob);
    },
    //上传图片1
    getFile1(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          })
            .then((data) => {
              this.license1 = data.data.data.plateNumber;
              let date = data.data.data.registerDate?data.data.data.registerDate:"";
              let date2 = data.data.data.productDate?data.data.data.productDate:"";
              if(date!=""&&new RegExp(/\d{4}\d{2}\d{2}/).test(date)){
                this.formData.registerDate =
                  date.slice(0, 4) +
                  "-" +
                  date.slice(4, 6) +
                  "-" +
                  date.slice(6, 8);
              }
              if(date2!=""&&new RegExp(/\d{4}\d{2}\d{2}/).test(date2)){
                this.formData.productDate =
                  date2.slice(0, 4) +
                  "-" +
                  date2.slice(4, 6) +
                  "-" +
                  date2.slice(6, 8);
              }
              //车牌号码
              this.formData.plateNumber = data.data.data.plateNumber;
              //车辆识别代码
              this.formData.vin = data.data.data.vin;
              //车辆类型 这里只给提供了名称没有提供id 所以需要去查找所对应id赋值
              (this.formData.vehicleTypeId = this.vehicleTypeOptions[
                this.vehicleTypeOptions
                  .map((item) => item.vehicleTypeName)
                  .indexOf(data.data.data.vehicleTypeName)
                ].vehicleTypeId),
                //所有人
                (this.formData.owner = data.data.data.owner);
              //图片地址
              this.formData.picture1 = data.data.data.imageURL;
              //发动机号码
              this.formData.engineModel = data.data.data.engineModel;
              this.formData.address = data.data.data.address;
              //步骤条
              this.active = 1;
            })
        });
      });
    },
    //删除图片1
    removeFile1(file, fileList) {
       this.picture1fileList = [];
      //步骤条
      this.active = 0;
    },
    //上传图片3
    getFile3(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense2`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          })
            .then((data) => {
              this.license2 = data.data.data.license;
              this.formData.approvedLoad = data.data.data.approvedLoad.split("kg")[0]
              this.formData.appprovedPassengerCapacity = getNum(data.data.data.appprovedPassengerCapacity.split("人")[0]);
              this.formData.picture3 = data.data.data.imageURL;
              this.active = 1;
            })
            .then(() => {
              //需求改变上传后不需要回显
              //this.getCarInfo();
            });
        });
      });
    },
    //删除图片1
    removeFile3(file, fileList) {
       this.picture3fileList = [];
      this.active = 0;
    },
    //上传图片2
    getFile2(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            //起初约定好的规则 多张图片用逗号隔开的字符串传输
            if (this.formData.picture2 == "") {
              this.formData.picture2 = data.data.data;
            } else {
              this.formData.picture2 =
                this.formData.picture2 + "," + data.data.data;
            }
          });
        });
      });
    },
    //删除图片2
    removeFile2(file, fileList) {
      this.formData.picture2 = "";
      this.picture2fileList = [];
    },
    pageInit() {
      // let testUrl =
      //   "http://park.leting360.cn/app/index.html#?parkId=2628ee995d6da9769f8e5ae9dc3d10a7&accessToken=41040300140120110600001&userName=TEST#/";
      // console.log(window.location.href, "地址栏url");
      // let url = window.location.href;
      // //这里的url要切换
      // let urlData = quryToObj(url.split('#/')[0]);
      // parkId = urlData.parkId;
      // accessToken = urlData.accessToken;
      // userName = urlData.userName;
      // console.log(urlData, "获取后的");
      this.parkId = this.$route.query.parkId;
      this.accessToken = this.$route.query.accessToken;
      this.userName = this.$route.query.userName;
      this.accessPlatform = this.$route.query.accessPlatform;
      //车辆类型下拉接口
      this.Axios.post(`/openapi/dztz/getVehicleTypeList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.vehicleTypeOptions = data.data.data;
      });
      //排放阶段下拉接口
      this.Axios.post(`/openapi/dztz/getEmissionStageList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.emissionOptions = data.data.data;
      });
    },
    //把图片文件作为参数传递到方法中 num 1 主页 2 随车清单照片 3 副页 4 车辆照片
    ys(file, num) {
      return new Promise((resolve) => {
        let isLt2M = file.file.size / 1024 / 1024< 2;
        if (isLt2M) {
          resolve(file.file);
        } else {
          imageConversion
             .compressAccurately(file.file, 800*2)
            .then((res) => {
              resolve(res);
            });
        }
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.box {
  padding: 10px;
}
/deep/.hide .el-upload--picture-card {
  display: none;
}

.el-row {
  margin-bottom: 20px;
}
</style>



