import Vue from 'vue'
import Router from 'vue-router'
import carRegister from '@/components/carRegister'
import paizhao from '@/components/paizhao'
import vehicleType from "@/components/vehicleType"
import fieldCarRegister from "@/components/fieldCarRegister"
import mechanicsCarRegister from "@/components/mechanicsCarRegister"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'vehicleType',
      component: vehicleType
    },
    {
      path: '/fieldCarRegister',//场内运输车辆
      name: 'fieldCarRegister',
      component: fieldCarRegister
    },
    {
      path: '/mechanicsCarRegister',//非道路移动器械
      name: 'mechanicsCarRegister',
      component: mechanicsCarRegister
    },
    {
      path: '/carRegister',//运输车辆
      name: 'carRegister',
      component: carRegister
    },
    {
      path: '/paizhao',
      name: 'paizhao',
      component: paizhao
    }
  ]
})



